body {
    margin: auto;
    -ms-flex-line-pack: center;
        align-content: center;
    text-align: center;
    height: auto;
    max-width: 100%;
}

#landingPage {
    background: -webkit-gradient(linear, left top, left bottom, color-stop(24%, rgba(204,204,204,1)), color-stop(75%, rgba(143,143,143,0)));
    background: -webkit-linear-gradient(top, rgba(204,204,204,1) 24%, rgba(143,143,143,0) 75%);
    background: -o-linear-gradient(top, rgba(204,204,204,1) 24%, rgba(143,143,143,0) 75%);
    background: linear-gradient(180deg, rgba(204,204,204,1) 24%, rgba(143,143,143,0) 75%);
    background-color: white;
    height: auto;
}


